import BasicModal from '@/modal/basic'    // 확인 모달
import SideMenu from '../sideMenu'        // 고객센터 사이드 메뉴

export default {
  name: 'supportDetail',
  // layout: 'default',
  components: {
    BasicModal,
    SideMenu
  },
  data: () => ({
    basicModal : false,
    select    : true,
    navTit     :'이용안내',
    navNow     :'출금',
    goodCount  :'64',
    board:{
      tit   :'코인을 보유하고 있는데 왜 출금이 안 되나요?',
      date  :'2022.03.22',
      count :'32561',
      txt   :'아래의 상황에서는 디지털 자산 출금이 제한되니 참고하시기 바랍니다.',
    }
  }),
  methods: {
    goodBtn(){
      this.basicModal = true;
      setTimeout(() => {
        this.basicModal = false;
      }, 1200)
      this.select     = true
    },
    badBtn(){
      this.basicModal = true;
      setTimeout(() => {
        this.basicModal = false;
      }, 1200)
      this.select     = false
    },
    close(){
      this.basicModal = true
    }
  }
}
